import Grid from "@material-ui/core/Grid";
import WithPadding from "components/shared/WithPadding";
import { Formik } from "formik";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import SwiperCore, { Autoplay, Pagination } from "swiper/core";
import { Swiper, SwiperSlide } from "swiper/react";
import { getRequest } from "utils/api";
import waveSvg from "../../assets/images/careerPedia/Wave.svg";
import Image from "./Base.svg";
import style from "./BlogPreview.module.scss";

SwiperCore.use([Pagination, Autoplay]);

export default function BlogPreview() {
  const navigateTo = useNavigate();

  const [blogs, setBlogs] = useState([]);
  const [categories, setCategories] = useState([]);

  const [isLoading, setIsLoading] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [search, setSearch] = useState("");

  const [formData, setFormData] = useState({ email: "" });

  async function fetchBlogs(category, search) {
    setIsLoading(true);

    const params = {};

    if (category && category !== "all") {
      params.category = category;
    }

    if (search && search !== "") {
      params.search = search;
    }

    try {
      const res = await axios.get(`${config.apiBaseUrl}/web/blogs`, { params });
      setBlogs(res.data);
    } catch {
    } finally {
      setIsLoading(false);
    }
  }

  function handleChangeForm(e) {
    const { value } = e.target;
    setFormData({ email: value });
  }

  function fetchCategories() {
    getRequest(`/web/blog-categories`, { noAuth: true })
      .then((resp) => {
        setCategories(resp.data);
      })
      .catch((err) => {
        console.error(err);
      });
  }

  async function handleGetStarted() {
    const { email } = formData;

    postRequest(
      "/web/subscriptions",
      {
        email,
      },
      { noAuth: true }
    )
      .then(() => {
        navigateTo(`/auth/register?email=${email}`);
      })
      .catch((e) => {
        console.error(e);
      });
  }

  useEffect(() => {
    fetchCategories();
  }, []);

  useEffect(() => {
    fetchBlogs(selectedCategory, search);
  }, [selectedCategory]);

  return (

    <div className={style.getTouch}>
      <img className={style.wave_svg} src={waveSvg} alt="Wave SVG" />
      <div className={style.blackBackground}>
        <WithPadding>
          <Grid container spacing={3} className={style.container}>

            <Grid item xs={12} sm={12} md={6} lg={6}>
              <div className={style.heading}>Empower Your Career Decisions</div>
              <Formik initialValues={formData} onSubmit={handleGetStarted}>
                {({ handleSubmit }) => (
                  <form onChange={handleChangeForm} onSubmit={handleSubmit} className={style.emailBox}>
                    <input type="email" placeholder="Enter your email" value={formData.email} />
                    <button type="submit">Get Started</button>
                  </form>
                )}
              </Formik>
              <div className={style.explanation}>Join the CareerTest.ai community and discover your best-fit career paths</div>
            </Grid>

            <Grid item xs={12} sm={12} md={6} lg={6}>
              <div className={style.swipergroup}>
                <Swiper
                  pagination={{ clickable: true }}
                  navigation={false}
                  slidesPerView={1}
                  spaceBetween={50}
                  className={style.swiperContainer}
                  allowTouchMove={true}
                  speed={1000}
                  autoplay={{ delay: 3000 }}
                  loop={true}>
                  <SwiperSlide>
                    <div className={style.maincard}>
                      <div className={style.card}>
                        <img src={Image} className={style.cardimg} alt="Card" />
                        <div className={style.cardnamediv}>
                          <h3>The best aticles every week</h3>
                          <p>
                            Our insurance plans offers are priced the same
                            everywhere else.
                          </p>
                        </div>
                      </div>
                    </div>
                  </SwiperSlide>
                  <SwiperSlide>
                    <div className={style.maincard}>
                      <div className={style.card}>
                        <img src={Image} className={style.cardimg} alt="Card" />
                        <div className={style.cardnamediv}>
                          <h3>The best aticles every week</h3>
                          <p>
                            Our insurance plans offers are priced the same
                            everywhere else.
                          </p>
                        </div>
                      </div>
                    </div>
                  </SwiperSlide>
                </Swiper>
              </div>
            </Grid>

          </Grid>
        </WithPadding>
      </div>
    </div>
  );
}
